<template>
    <div class="login">
        <header data-am-widget="header" class="am-header am-header-default my-header">
            <div class="am-header-left am-header-nav">
                <a href="javascript:;" onclick="window.history.go(-1)">
                    <i class="am-header-icon am-icon-chevron-left"></i>
                </a>
            </div>
            <h1 class="am-header-title">
                <a href="#title-link" class="">用户登录</a>
            </h1>
            <div class="am-header-right am-header-nav">
                <a href="#right-link" class="">
                    <i class="am-header-icon  am-icon-home"></i>
                </a>
            </div>
        </header>
        <div class="cart-panel">
            <form class="am-form">
                <div class="am-form-group am-form-icon">
                    <i class="am-icon-user" style="color:#329cd9"></i>
                    <input type="text" class="am-form-field  my-radius" value="alex" placeholder="请输入您的用户名" v-model="username">
                </div>
                <div class="am-form-group am-form-icon">
                    <i class="am-icon-lock" style="color:#329cd9"></i>
                    <input type="password" class="am-form-field my-radius" value="alex123" placeholder="请输入您的密码" v-model="password">
                </div>
                <div class="am-checkbox">
                    <label>
                        <input type="checkbox"> 记住密码
                    </label>
                </div>
                <p class="am-text-center"><button type="button" class="am-btn am-btn-danger am-radius am-btn-block" @click="login">立即登录</button></p>
                <p class="am-text-center">{{error}}</p>
            </form>
        </div>
    </div>
</template>
<style scoped>
    .am-checkbox,.am-text-center{
        margin:0.9rem .7rem;
    }
    .am-form-group{
        margin:.9rem .7rem;
    }
    .am-header .am-header-title {
        margin: 0 30%;
        font-size: 2rem;
        font-weight: normal;
        text-align: center;
        display: block;
        word-wrap: normal;
        /* for IE */
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
    .am-header .am-header-title img {
        margin-top: 12px;
        height: 25px;
        vertical-align: top;
    }
  .login .am-header-default{
         background: #d7342e;}
    .am-header-default .am-header-title {
        color: #ffffff;
    }
    .am-header-default .am-header-title a {
        color: #ffffff;
    }

</style>
<script>
    // import axios from 'axios';
    export  default {
        data(){
            return{
                username:"",
                password:"",
                error:""
            }
        },
        methods:{
            login(){
                axios.post("/api/login",{
                    data:{username: this.username,password: this.password}
                }).then(
                    res=>{
                        if (res.data.error==0) {
                            this.$router.push('/user')
                        }else {
                            this.error=res.data.msg
                        }
                    }
                )
            }

        },
        mounted(){
            this.$root.bool = false;

        }

    }
</script>