<template>
    <div class="detail">
        <div class="top">
            <img :src="detail && 'http://101.132.165.134:3000'+detail.detail.auth_icon">
                <h3><span>  产品名：</span>{{detail.title}}</h3>

                <h3><span>产品介绍：</span>大幅降低房价司法所覅的身份函数返回的是发单号就发我i额</h3>
        </div>
        <dl>
            <dt>商品名称:{{detail.title}}</dt>
            <dd>单价:{{detail.des}}</dd>
            <dd class="dd1"><em>数量：</em> <i @click="jian">-</i><input type="number"min="0" v-model="cont"><i @click="cont+=1">+</i></dd>
            <dd>小计:{{cont*detail.des}} </dd>
        </dl>
            <div>
                <ul class="am-avg-sm-2 am-text-center">
                    <li class="am-text-center am-padding-sm" @click=""><button type="button" class="am-btn am-btn-success am-btn-block am-radius">加入购物车</button></li>
                    <router-link to="/buycar" tag="li" class="am-text-center am-padding-sm"><button type="button" class="am-btn am-btn-danger am-btn-block am-radius">立即购买</button></router-link>
                </ul>
            </div>
    </div>
</template>
<script>
    export default {
      data(){
            return{
                cont:0,
                detail:'',
                baseUrl:baseUrl
            }},
        methods:{
          jian(){
              this.cont-=1 && this.cont>=1;
          }
        },
      beforeRouteEnter(to,from,next) {
          axios({
              url:'/api/detail',
              params:{dataName:to.query.dataName,_id:to.params.id}
          }).then(
              res=>{
                  next(_this=>{
                      _this.detail=res.data.page_data;
                  });
              }
          )
      }
    }
</script>
<style>
    .detail{background:#f3f3fa}
    .top{overflow: hidden;color: #2f333e}
    .top img{width: 20rem;height: 20rem;float: left;margin: 2rem}
    .detail dl{margin: 2rem}
    .detail dl dd{ margin: 1rem 0;height: 3em}
    .detail dl .dd1{}
    .detail dl .dd1 input{height:3rem;width:10rem;left: 1rem;
        float: left;text-align: center}
    .detail dl .dd1 em{ float: left;}
    .detail dl dd i{display: inline-block;font-size: 3rem;width:3rem;float: left;line-height: 3rem;text-align: center;background: #c9e7c9;color: #0a628f}
</style>

